import React, { useState } from "react";
import classes from "./ProfileInfo.module.css";
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatus from "./ProfileStatus";
import { Button, Fab } from "@material-ui/core";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import ProfileDataForm from "./ProfileDataForm";
import EditIcon from "@material-ui/icons/Edit";

const ProfileInfo = (props) => {

  const [editMode, setEditMode] = useState(false)

  if (!props.profile) {
    return <Preloader />
  }

const onMainPhotoSelected = e => {
  if (e.target.files.length) {
    props.savePhoto(e.target.files[0]);
  }
};


const onSubmit = (formData) => {
  props.saveProfile(formData).then(() => {
    setEditMode(false);
  });
};


  return <div>
      <div className={classes.descriptionBlock}>
        <img src={props.profile.photos.large || "https://cdn.iconscout.com/icon/free/png-512/avatar-370-456322.png"} alt="" />

        {props.isOwner ? <div className={classes.uploadBtn}>
            <input accept="image/*" className={classes.input} id="contained-button-file" type={"file"} onChange={onMainPhotoSelected} />
            <label htmlFor="contained-button-file">
              <Button size="small" variant="contained" color="default" component="span" startIcon={<CloudUploadIcon />}>
                Upload Photo
              </Button>
            </label>
          </div> : null}

        {editMode ? <ProfileDataForm initialValues={props.profile} profile={props.profile} onSubmit={onSubmit} /> : <ProfileData ToEditMode={() => setEditMode(true)} profile={props.profile} isOwner={props.isOwner} />}
        <div>
          {editMode ? null : (
            <ProfileStatus
              status={props.status}
              updateStatus={props.updateStatus}
            />
          )}
        </div>
      </div>
    </div>;
};


const ProfileData = ({profile, isOwner, ToEditMode}) => {
  return <div>
      <div>
        <span className={classes.fullName}>{profile.fullName}</span>
          {isOwner && 
              <Fab color="primary" aria-label="edit">
                <EditIcon onClick={ToEditMode} />
              </Fab>
            }
        </div>
      <div>
        <b className={classes.about}>About me:</b>
        {profile.aboutMe}
      </div>
      <div>
        <b>Looking for a job:</b> {profile.lookingForAJob ? "yes" : "no"}
      </div>
      {profile.lookingForAJob && <div>
          <b>My professional skills</b>: {profile.lookingForAJobDescription}
        </div>}
      <div>
        <b>Contacts:</b> {Object.keys(profile.contacts).map((key) => (
          <Contact
            key={key}
            contactTitle={key}
            contactValue={profile.contacts[key]}
          />
        ))}
      </div>
    </div>;
}


export const Contact = ({contactTitle, contactValue}) => {
    return <div className={classes.contacts}>
        <b>{contactTitle}</b>: {contactValue}
      </div>;
}

export default ProfileInfo;

