import React from 'react';
import classes from "./ProfileInfo.module.css";
import styles from "../../common/FormsControls/FormsControls.module.css";
import { createField, TextareaJsx, CheckboxForm } from '../../common/FormsControls/FormsControls';
import { reduxForm } from 'redux-form';
import DoneOutlineRoundedIcon from "@material-ui/icons/DoneOutlineRounded";
import { Button } from '@material-ui/core';

 const ProfileDataForm = ({ handleSubmit, profile, error }) => {
  return <form onSubmit={handleSubmit} autoComplete="off">
      <div className={classes.editFullName}>
        <b>Full name</b>: {createField("Full name", "fullName", TextareaJsx, [])}
      </div>
      <div className={classes.aboutInput}>
        <b>About me:</b>
        {createField("About me", "aboutMe", TextareaJsx, [])}
      </div>
      <div>
        <b>Looking for a job:</b> {createField(CheckboxForm, "LookingForAJob", CheckboxForm, [])}
      </div>
      <div className={classes.aboutInput}>
        <b>My professional skills</b>:{createField("My skills", "lookingForAJobDescription", TextareaJsx, [])}
      </div>

      <div>
        <b>Contacts:</b> {Object.keys(profile.contacts).map((key) => (
          <div key={key} className={classes.contact}>
            <b>
              {key}:{createField(key, "contacts." + key, TextareaJsx, [])}
            </b>
          </div>
        ))}
        {error ? <div className={styles.formSummaryError}>
            {error}
          </div> : null}
        <div className={classes.namesAbout}>
          <Button variant="contained" color="primary" size="small" className={classes.button} type="submit">
            <DoneOutlineRoundedIcon />
          </Button>
        </div>
      </div>
    </form>;
};

const ProfileDataFormRedux = reduxForm({
   form: 'edit-profile'
})(ProfileDataForm)

export default ProfileDataFormRedux;

